.container {
    position: fixed;
    top: 24px;
    right: 24px;
    display: flex;
    align-items: center;
    gap: var(--space-4);
    padding: var(--space-4) var(--space-5);
    background: linear-gradient(135deg,
            rgba(139, 92, 246, 0.2) 0%,
            rgba(236, 72, 153, 0.1) 100%);
    border: 1px solid rgba(139, 92, 246, 0.4);
    border-radius: var(--radius-xl);
    z-index: var(--z-toast);
    overflow: hidden;
    max-width: 360px;
}

.glow {
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle at 30% 30%,
            rgba(139, 92, 246, 0.3) 0%,
            transparent 50%);
    animation: glowPulse 3s ease-in-out infinite;
}

@keyframes glowPulse {

    0%,
    100% {
        opacity: 0.5;
    }

    50% {
        opacity: 1;
    }
}

.iconContainer {
    width: 56px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--primary-600), var(--accent-pink));
    border-radius: var(--radius-lg);
    flex-shrink: 0;
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.5);
}

.icon {
    font-size: 1.75rem;
}

.content {
    flex: 1;
    min-width: 0;
    position: relative;
    z-index: 1;
}

.label {
    display: block;
    font-size: 0.6875rem;
    font-weight: 600;
    color: var(--accent-pink);
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-bottom: 2px;
}

.title {
    font-size: 1rem;
    font-weight: 700;
    font-family: var(--font-display);
    color: var(--text-primary);
    margin: 0 0 2px 0;
    line-height: 1.3;
}

.description {
    font-size: 0.8125rem;
    color: var(--text-muted);
    margin: 0;
    line-height: 1.4;
}

.closeButton {
    position: absolute;
    top: var(--space-2);
    right: var(--space-2);
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 50%;
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.closeButton:hover {
    background: rgba(255, 255, 255, 0.2);
    color: var(--text-primary);
}

@media (max-width: 480px) {
    .container {
        top: auto;
        bottom: 24px;
        left: 16px;
        right: 16px;
        max-width: none;
    }
}